.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes visible1 {
  from {
    opacity: 0;
    /* left: 0; */
    transform: translateY(150px) translateX(150px);
  }
  to {
    opacity: 100;
    /* left: 1000; */
    transform: translateY(0px) translateX(0px);
  }
}
@keyframes comingFromRightAn {
  from {
    opacity: 0;
    /* left: 0; */

    transform: translateX(150px);
  }
  to {
    opacity: 100;
    /* left: 1000; */
    transform: translateX(0px);
  }
}
@keyframes comingFromLeftAn {
  from {
    opacity: 0;
    /* left: 0; */
    transform: translateX(-150px);
  }
  to {
    opacity: 100;
    /* left: 1000; */
    transform: translateX(0px);
  }
}

@keyframes comingFromUpAn {
  from {
    opacity: 0;
    /* left: 0; */
    transform: translateY(-150px);
  }
  to {
    opacity: 100;
    /* left: 1000; */
    transform: translateY(0px);
  }
}

@keyframes comingFromDownAn {
  from {
    opacity: 0;
    /* left: 0; */
    transform: translateY(150px);
  }
  to {
    opacity: 100;
    /* left: 1000; */
    transform: translateY(0px);
  }
}

@keyframes growOutAn {
  from {
    opacity: 0;
    width: 0%;
    height: 0%;
  }
  to {
    opacity: 100;
    width: 100%;
    height: 100%;
  }
}

