.growOutP5 {
  animation: growOutAn 0.5s;
}
.growOut {
  animation: growOutAn 1s;
}
.comingFromLeft {
  animation: comingFromLeftAn 1s;
}
.comingFromRight {
  animation: comingFromRightAn 1s;
}
.comingFromDown {
  animation: comingFromDownAn 1s;
}
.comingFromUp {
  animation: comingFromUpAn 1s;
}

.comingFromLeftP5 {
  animation: comingFromLeftAn 0.5s;
}
.comingFromRightP5 {
  animation: comingFromRightAn 0.5s;
}
.comingFromDownP5 {
  animation: comingFromDownAn 0.5s;
}
.comingFromUpP5 {
  animation: comingFromUpAn 0.5s;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.box {
  margin-top: 0;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.spin {
  display: inline-block;
  width: 150px;
  height: 150px;
  border: 10px solid rgba(000, 000, 000, 0.1);
  border-radius: 50%;
  border-top-color: rgba(000, 000, 000, 0.3);
  animation: spin 1s ease-in-out infinite;
  -webkit-animation: spin 1s ease-in-out infinite;
}
@keyframes spin {
  to {
    -webkit-transform: rotate(360deg);
  }
}
@-webkit-keyframes spin {
  to {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes arrowRightA {
  0% {
    transform: translateX(0px);
  }
  50% {
    transform: translateX(10px);
  }
  100% {
    transform: translateX(0px);
  }
}

.arrowRight {
  animation: arrowRightA 2s infinite;
}

@keyframes arrowLeftA {
  0% {
    transform: translateX(0px);
  }
  50% {
    transform: translateX(-10px);
  }
  100% {
    transform: translateX(0px);
  }
}

@keyframes arrowLeftA100 {
  0% {
    transform: translateX(0px);
  }
  50% {
    transform: translateX(-100px);
  }
  100% {
    transform: translateX(0px);
  }
}

.arrowLeft {
  animation: arrowLeftA 2s infinite;
}

.arrowLeft100 {
  animation: arrowLeftA100 2s infinite;
}

@keyframes coming {
  from {
    opacity: 0;
    /* left: 0; */
    transform: translateX(-1500px);
  }
  to {
    opacity: 100;
    /* left: 1000; */
    transform: translateX(0px);
  }
}

.notificationSlide {
  animation: coming 1s;
}

.input-field {
  /* width: 100%; */
  /* margin-left: 10px; */
}

.input-field label {
  position: absolute;
  display: block;
  float: left;
  font-size: medium;

  /* font-size: 10px; */
  transform: translateY(-28px);
  transition: 0.15s;
  font-family: "Poppins";
  font-weight: bold;
  color: grey;
  pointer-events: none;
  /* position: absolute; */
  /* top: 0; */
  /* left: 0; */
}

.input-field input {
  position: relative;
  font-family: "Poppins";
  color: grey;
  font-weight: 600;
  /* padding: 10px; */
  /* border-radius: 5px; */
  width: 250px;
  font-size: medium;
  padding: 10px;
  /* border-radius: 10px; */
  height: 30px;
  font-size: medium;
  /* padding-left: 10; */
  /* height: 40px; */
  /* padding: 10; */
  background: transparent;
  transition: 0.1s;
  border: none;
  border-bottom: 3px solid grey;
}
.moveUP {
  display: inline-block;
  transition: 1s;
}
.moveUP:hover {
  transform: translateY(-5px);
}
.input-field input:focus {
  /* transform: translateY(-40px); */
  /* font-size: small; */
  outline: none;
  border-bottom: 3px solid grey;
}

.input-field input:focus + label {
  transform: translateY(-48px);
  font-size: small;
  /* margin-left: 0px; */
  color: orange;
}

.input-field input ~ .focus-border {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 2px;
  background-color: #3399ff;
  transition: 0.4s;
}
.input-field input:focus ~ .focus-border {
  width: 100%;
  transition: 0.4s;
}

.input-field input:valid + label {
  transform: translateY(-48px);
  /* margin-left: 0px; */
  font-size: small;
}

input.is-danger-field {
  border-bottom: 3px solid red;
}

.col-3 {
  float: left;
  width: 27.33%;
  margin: 40px 3%;
  position: relative;
}

.effect-1,
.effect-2,
.effect-3 {
  border: 0;
  padding: 7px 0;
  border-bottom: 1px solid #ccc;
}
.effect-1 ~ .focus-border {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 2px;
  background-color: #3399ff;
  transition: 0.4s;
}
input[type="text"] {
  font: 15px/24px "Lato", Arial, sans-serif;
  color: #333;
  width: 100%;
  box-sizing: border-box;
  letter-spacing: 1px;
}
:focus {
  outline: none;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.effect-1:focus ~ .focus-border {
  width: 100%;
  transition: 0.4s;
}

.responsiveCard {
  width: 600px;
}

@media only screen and (max-width: 600px) {
  .responsiveCard {
    width: 600px;
  }
}

