$color-primary: #000000;

.input-group {
  position: relative;
}
.form-control {
  width: 100%;
  height: 35px;
  font-size: 14px;
  font-family: poppins;
  font-weight: bold;
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 5px;

  border: none;

  background-color: transparent;
  & + label {
    position: absolute;
    top: 10px;
    left: 3px;
    font-family: poppins;
    font-weight: bolder;
    transition: 0.1s;
    outline: none;
    font-size: 16px;
    color: lighten($color: #000000, $amount: 40%);
  }
  &:focus {
    outline: none;
    transition: 0.1s;
  }

  &:focus + label,
  &:hover + label {
    transition: 0.1s;
    transform: translateY(-25px);
    color: #00c421;
    font-size: small;
  }

  & ~ .line {
    width: 0%;
    transition: all 0.2s ease-in-out;
  }

  &:focus ~ .line {
    position: absolute;
    left: 0;
    top: 32.5px;
    width: 101.5%;
    border-radius: 10px;
    background: $color-primary;
    transition: 0.3s ease-in-out;
    height: 3px;
    z-index: 1;
  }
}
.input-group > .has-value {
  transition: 0.1s;
  transform: translateY(-25px);
  color: #00c421;
  font-size: small;
}
